import React, { useState } from 'react'
import './ShoppingCart.css'
import { useStateValue } from './StateProvider';

function ShoppingCart({ id, title, price, image, rating }) {
    const [{ basket }, dispatch] = useStateValue();

    const removeFromBasket = () => {
        dispatch({
            type: 'REMOVE_FROM_BASKET',
            id: id,
        })

    }
    return (
        <div className="shoppingCart">
            <img className="shoopingCart__image" src={image} alt="" />
            <div className="shoppingCart__info">
                <p className="shoppingCart__title">{title}</p>
                <p className="shoppingCart__price">
                    <small>$</small>
                    <strong>{price}</strong>
                </p>
                <div className="shoppingCart__rating">
                    {Array(rating)
                        .fill()
                        .map((_, i) => (
                            <p>🌟</p>
                        ))}
                </div>
                <button onClick={removeFromBasket}>Remove from Basket</button>
            </div>
        </div>
    )
}

export default ShoppingCart

